$mainColor : #ff0000;

.red{
	color: $mainColor;
}

@mixin name {
	background: #eeeeee;
	padding: 20px;
	border-radius: 5px;
	max-width: 500px;
	width: 100%;
	margin: auto;
}

.my_alert {
	@include name()
}

.my_alert2 {
	@extend .my_alert;
	background: #ffe591;
}


.my_alert p{
	margin-bottom: 0;
}
